      (detailFormRef.value as any).open({ ...row}); // ‰º†ÈÄí `row` ‰Ωú‰∏∫ÂèÇÊï∞
    } else {
      console.error("DetailForm ÁªÑ‰ª∂Êú™Ê≠£Á°ÆÊåÇËΩΩÔºÅ");
    }
  });
};
//
// // ‰ΩøÁî®Ë°®Ê†ºÊèí‰ª∂Êù•ÁÆ°ÁêÜË°®Ê†ºÁöÑÁä∂ÊÄÅ„ÄÅÊï∞ÊçÆÁ≠â
// const {
//   handleSelectionChange,
//   hasSelectedRows,
//   isVisible: showSearch,
//   toggleVisibility: toggleSearch,
//   loadData,
//   data,
//   refreshData,
//   handleSizeChange,
//   handleCurrentChange,
//   searchParams,
//   resetSearchParams
// } = useTableDataPlugin("/api/v1/admin/order");
const {
  handleSelectionChange,
  hasSelectedRows,
  selectedRows,
  isVisible: showSearch,
  toggleVisibility: toggleSearch,
  loadData,
  data,
  refreshData,
  handleSizeChange,
  handleCurrentChange,
  searchParams
} = useTableDataPlugin("/api/v1/admin/order");

// ËÆæÁΩÆÊêúÁ¥¢Ê°ÜÈªòËÆ§Â±ïÂºÄ
showSearch.value = true;
// Á°Æ‰øùÊêúÁ¥¢ÂèÇÊï∞ÊúâÊ≠£Á°ÆÁöÑÁªìÊûÑ
if (!(searchParams.value as any).search) {
  (searchParams.value as any).search = {};
}

// Ê†ºÂºèÂåñÊú¨Âú∞Êó∂Èó¥
const formatLocalDateTime = (date: Date) => {
  const year = date.getFullYear();
  const month = String(date.getMonth() + 1).padStart(2, '0');
  const day = String(date.getDate()).padStart(2, '0');
  const hours = String(date.getHours()).padStart(2, '0');
  const minutes = String(date.getMinutes()).padStart(2, '0');
  const seconds = String(date.getSeconds()).padStart(2, '0');
  return `${year}-${month}-${day} ${hours}:${minutes}:${seconds}`;
};

// ÂàùÂßãÂåñÊêúÁ¥¢ÂèÇÊï∞
const initSearchParams = () => {
  if (!(searchParams.value as any).search) {
    const today = new Date();
    const startOfDay = new Date(today.getFullYear(), today.getMonth(), today.getDate(), 0, 0, 0);
    const endOfDay = new Date(today.getFullYear(), today.getMonth(), today.getDate(), 23, 59, 59);
    
    (searchParams.value as any).search = {
      order_no: '',
      merchant_order_no: '',
      merchant_id: '',
      status: '',
      start_time: formatLocalDateTime(startOfDay),
      end_time: formatLocalDateTime(endOfDay)
    };
  }
};

// Ëé∑Âèñ‰ªäÂ§©ÁöÑÂºÄÂßãÂíåÁªìÊùüÊó∂Èó¥
const getTodayTimeRange = () => {
  const today = new Date();
  const startOfDay = new Date(today.getFullYear(), today.getMonth(), today.getDate(), 0, 0, 0);
  const endOfDay = new Date(today.getFullYear(), today.getMonth(), today.getDate(), 23, 59, 59);
  
  return [
    formatLocalDateTime(startOfDay),
    formatLocalDateTime(endOfDay)
  ];
};

// ÊêúÁ¥¢Ë°®ÂçïÊï∞ÊçÆ
const searchForm = ref({
  order_no: '',
  merchant_order_no: '',
  merchant_id: '',
  status: '',
  timeRange: getTodayTimeRange() as [string, string] | null
});

// ÂïÜÊà∑ÂàóË°®
const merchantList = ref<any[]>([]);

// Áî®Êà∑‰ø°ÊÅØ
const userInfo = ref<any>(null);

// ÊêúÁ¥¢Âä†ËΩΩÁä∂ÊÄÅ
const searchLoading = ref(false);

// ÁªüËÆ°Êï∞ÊçÆ
const statistics = ref<any>(null);

// Ê†ºÂºèÂåñÈáëÈ¢ù
const formatAmount = (amount: number | string) => {
  if (!amount) return '0.00';
  return Number(amount).toFixed(2);
};

// Ê†ºÂºèÂåñÁôæÂàÜÊØî
const formatPercentage = (rate: number | string) => {
  if (!rate) return '0.00';
  return Number(rate).toFixed(2);
};

// Ëé∑ÂèñÁî®Êà∑‰ø°ÊÅØ
const loadUserInfo = async () => {
  try {
    const res = await request.get('/api/v1/admin/user/info');
    console.log('Áî®Êà∑‰ø°ÊÅØAPIÂìçÂ∫î:', res);
    if ((res as any).code === 200) {
      userInfo.value = (res as any).data;
      console.log('Áî®Êà∑‰ø°ÊÅØ:', userInfo.value);
      console.log('ÊòØÂê¶‰∏∫ÂïÜÊà∑ÁÆ°ÁêÜÂëò:', userInfo.value.is_merchant_admin);
    }
  } catch (error) {
    console.error('Ëé∑ÂèñÁî®Êà∑‰ø°ÊÅØÂ§±Ë¥•:', error);
  }
};

// Ëé∑ÂèñÂïÜÊà∑ÂàóË°®
const loadMerchantList = async () => {
  try {
    const res = await request.get('/api/v1/admin/merchant', {
      params: {
        page: 1,
        page_size: 1000, // Ëé∑ÂèñÊâÄÊúâÂïÜÊà∑
        search: JSON.stringify({})
      }
    });
    console.log('ÂïÜÊà∑APIÂìçÂ∫î:', res);
    if ((res as any).code === 200) {
      merchantList.value = (res as any).data.data || [];
      console.log('ÂïÜÊà∑ÂàóË°®:', merchantList.value);
    } else {
      console.log('ÂïÜÊà∑APIÂìçÂ∫îÈîôËØØ:', res);
    }
  } catch (error) {
    console.error('Ëé∑ÂèñÂïÜÊà∑ÂàóË°®Â§±Ë¥•:', error);
    merchantList.value = [];
  }
};

// Ëé∑ÂèñÁªüËÆ°Êï∞ÊçÆ
const loadStatistics = async (searchParams: any = {}) => {
  try {
    // ÊûÑÂª∫Êü•ËØ¢ÂèÇÊï∞
    const params: any = {};
    
    // Â¶ÇÊûúÊúâÊêúÁ¥¢ÂèÇÊï∞Ôºå‰º†ÈÄíÁªôÁªüËÆ°Êé•Âè£
    if (searchParams && Object.keys(searchParams).length > 0) {
      // Â§ÑÁêÜÊó∂Èó¥ËåÉÂõ¥
      if (searchParams.timeRange && searchParams.timeRange.length === 2) {
        params.start_time = searchParams.timeRange[0];
        params.end_time = searchParams.timeRange[1];
      }
      
      // ‰º†ÈÄíÂÖ∂‰ªñÊêúÁ¥¢ÂèÇÊï∞
      if (searchParams.order_no) params.order_no = searchParams.order_no;
      if (searchParams.merchant_order_no) params.merchant_order_no = searchParams.merchant_order_no;
      // ÂïÜÊà∑ÁÆ°ÁêÜÂëò‰∏çÈúÄË¶Å‰º†ÈÄímerchant_idÂèÇÊï∞ÔºåÂõ†‰∏∫Âè™ËÉΩÁúãÂà∞Ëá™Â∑±ÁöÑËÆ¢Âçï
      if (searchParams.merchant_id && !userInfo.value?.is_merchant_admin) {
        params.merchant_id = searchParams.merchant_id;
      }
      if (searchParams.status) params.status = searchParams.status;
      if (searchParams.start_time) params.start_time = searchParams.start_time;
      if (searchParams.end_time) params.end_time = searchParams.end_time;
    }
    
    console.log('ÁªüËÆ°Êé•Âè£ËØ∑Ê±ÇÂèÇÊï∞:', params);
    
    const res = await request.get('/api/v1/admin/order/statistics', { params });
    if ((res as any).code === 200) {
      statistics.value = (res as any).data;
      console.log('ÁªüËÆ°Êï∞ÊçÆÊõ¥Êñ∞:', statistics.value);
    }
  } catch (error) {
    console.error('Ëé∑ÂèñÁªüËÆ°Êï∞ÊçÆÂ§±Ë¥•:', error);
    // Â¶ÇÊûúAPI‰∏çÂ≠òÂú®Ôºå‰ΩøÁî®Ê®°ÊãüÊï∞ÊçÆ
    statistics.value = {
      total_orders: 0,
      total_amount: 0,
      paid_orders: 0,
      paid_amount: 0,
      merchant_income: 0,
      unpaid_orders: 0,
      unpaid_amount: 0,
      success_rate: 0
    };
  }
};


// Â§ÑÁêÜÊêúÁ¥¢
const handleSearch = async () => {
  searchLoading.value = true;
  try {
    // Â§ÑÁêÜÊó∂Èó¥ËåÉÂõ¥
    let searchData: any = { ...searchForm.value };
    if (searchData.timeRange && searchData.timeRange.length === 2) {
      searchData.start_time = searchData.timeRange[0];
      searchData.end_time = searchData.timeRange[1];
    }
    delete searchData.timeRange;
    delete searchData.quickTime;
    
    // Â∞ÜÊêúÁ¥¢Ë°®ÂçïÊï∞ÊçÆÂêåÊ≠•Âà∞searchParams
    (searchParams.value as any).search = searchData;
    
    // ÂÖàÂà∑Êñ∞Êï∞ÊçÆÔºåÂÜçÊõ¥Êñ∞ÁªüËÆ°Ôºà‰º†ÈÄíÊêúÁ¥¢ÂèÇÊï∞Ôºâ
    await refreshData();
    await loadStatistics(searchData);
  } catch (error) {
    console.error('ÊêúÁ¥¢Â§±Ë¥•:', error);
    ElMessage.error('ÊêúÁ¥¢Â§±Ë¥•ÔºåËØ∑ÈáçËØï');
  } finally {
    searchLoading.value = false;
  }
};

// Â§ÑÁêÜÈáçÁΩÆ
const handleReset = async () => {
  searchForm.value = {
    order_no: '',
    merchant_order_no: '',
