const handleAction = (command: string, row: any) => {
  switch (command) {
    case 'detail':
      openDetailFrom(row);
      break;
    case 'query':
      handleQuery(row);
      break;
    case 'refund':
      handleRefund(row);
      break;
    case 'resend':
      handleReissue(row);
      break;
    case 'callback':
      handleCallback(row);
      break;
    default:
      console.warn('Êú™Áü•Êìç‰Ωú:', command);
  }
};

